.header-color {
  background: #159957;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #155799, #159957);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #155799, #159957); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
}

.landing-grid{
  background: #159957;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #155799, #159957);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #155799, #159957); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
  text-align: center;
  position: fixed;
  top: 2;
  left: 0;
  width: 100%;
  height: 100%;
}

.navbar{
  position: fixed;
  font-size: medium;
  padding-left: 1%;
  width: 100%;
  background-color: white;
}

.navbar ul{
  list-style: none;
  text-align: left;
}

.navbar > .active {
  border-bottom: 3px solid black;
}
/* 
About Me
*/
.about-section{
  min-height: fit-content;
  background: #159957;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #155799, #159957);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #155799, #159957); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  text-align: center;
}

/* 
Experiences 
*/
.experiences-section{

  min-height: fit-content;
  background-color: aliceblue;
  padding: 1%;
}

.experiences-section h2 {
  text-align: center;
}


.skill_list {
  text-align: center;
  min-height: fit-content;
  padding: 1%;
  background-color: white;
}

/* 
Educations  
*/
.educations-section{
  min-height: fit-content;
  padding: 1%;
  background-color: white;
}

.educations-section h2 {
  text-align: center;
}

/* 
Projects
*/
.projects-section {
  min-height: fit-content;
  background-color: aliceblue;
  padding: 1%;
}

.projects-section h2 {
  text-align: center;
}

.projects-grid {
  display: flex;
  
}

.contact-section{
  min-height: fit-content;
  background-color: #155799;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-list i {
  font-size:25px;
  padding-right: 5rem;
}

.contact-grid h3{
  font-family: 'Anton', sans-serif;
}

.jake-img {
  height: 250px;
  padding-top: 5em;
}

.intro {
  background-color: black;
  opacity: .7;
  width: 80%;
  margin: auto;
  border-radius: 10px;
  
}


.banner-text{
  background-color: black;
  opacity: .7;
  width: 80%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  margin: auto;
  color: white;
}

.banner-text hr{
  border-top: 5px dotted white;
  width: 80%;
  margin: auto; 
}
.banner-text p{
  font-size: 20px;
  padding: 1em;
  color: white;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width:30%;
  margin: auto;
}

.social-links i {
  color:white;
  font-size:3em;
  padding: 8px;

}


.contact-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}


.contact-grid p{
  font-family: "oxygen", sans-serif;
  font-weight: bold;
}

.contact-grid hr{
  border-top: 5px dotted black;
  width: 100%;
  margin: auto;
}


/* Resume Page css*/
.resume-right-col {
  border-block-end-color: #155799;
  padding: 2em;
}

.about-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
}

